import React from 'react';
import { Link } from "react-router-dom";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import clsx from "clsx";
import { MDText } from 'i18n-react';

import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';

import pattern from '../../images/paterns/hexagonal.png';

import IconSearch from '@material-ui/icons/Search'

import colorYellow from "@material-ui/core/colors/lime";
import Grid from "@material-ui/core/Grid";
import colorGreen from "@material-ui/core/colors/lightGreen";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";

import logo_ejcm from '../../images/project_logos/ejcm.png';

import { drawerWidth, drawerMargin, containerMWdrawer, cardShiftSize } from '../styleDefinitions';
import { updateComponentsStatus } from "../../js/utilityFunctions";
import IconImportantDevices from "@material-ui/core/SvgIcon/SvgIcon";

import { textContent, research } from "./researchTexts";
import {systems} from "./systemsTexts";

const T = new MDText(textContent.pt, { MDFlavor: 1 });

const styles = (theme) => ({
    root: {
        flexGrow: 1,
        backgroundImage: 'url('+pattern+')',
        backgroundColor: '#f0f4c394',
        backgroundRepeat: 'repeat',
        padding: 0,
        // backgroundAttachment: 'fixed',
        margin: 0,
        width: '100%',
        maxWidth: '100%',
        boxShadow: 'inset 1px 2px 5px 1px rgba(0,0,0,0.15)',
    },
    container: {
        flexGrow: 1,
        width: '100%',
        maxWidth: '100%',
        backgroundImage: 'radial-gradient(#f2f5d78c, #f4fd8a30)',
        // backgroundAttachment: 'fixed',
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(7),
        margin: 0,
    },
    innerContainer: {
        padding: 0,
    },
    pageTitle: {
        // marginLeft: theme.spacing(2),
        marginBottom: theme.spacing(4),
        marginLeft: theme.spacing(1),
    },
    mainIcon: {
        fontSize: '140%',
        color: '#20201c23',
        verticalAlign: 'text-bottom',
        position: 'absolute',
        marginLeft: theme.spacing(1),
        marginTop: 0,
    },
    pageSubTitle: {
        // marginLeft: theme.spacing(2),
        marginLeft: theme.spacing(1.5),
    },
    gridContainer: {
        width: 'calc(100% + '+theme.spacing(2.5)+'px)',
        // marginRight: '0px !important',
        margin: -theme.spacing(1.5).toString() +'px !important',
    },
    gridItem: {
        [theme.breakpoints.down('md')]: {
            padding: theme.spacing(2).toString() +'px !important',
        },
        [theme.breakpoints.up('md')]: {
            padding: theme.spacing(2.5).toString() +'px !important',
        },
    },
    card: {
        height: '100%',
        backgroundColor: '#ffffff80',

        '&:hover, &.active, &:active': {
            backgroundColor: '#ffffffbe',
            boxShadow: '0px 2px 5px -1px rgba(0,0,0,0.2), 0px 4px 6px 0px rgba(0,0,0,0.14), 0px 1px 11px 0px rgba(0,0,0,0.12)', //el_5
            marginTop: -cardShiftSize,
            marginBottom: cardShiftSize,
        },

        transition: theme.transitions.create(['margin', 'background-color', 'box-shadow'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    header: {
        display: 'block',
        marginBottom: 0,
        paddingBottom: 0,
    },
    logo: {
        height: 50,
        display: 'block',
    },
    contentShift: {
        transition: theme.transitions.create('padding', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        paddingRight: drawerWidth + drawerMargin,
        paddingLeft: drawerMargin,
        maxWidth: containerMWdrawer,
    },
});

class Research extends React.Component {
    state = {
    };

    componentDidMount() {
        updateComponentsStatus(research[this.props.locale], this.props.activeComponent, 'Research');
        T.setTexts(textContent[this.props.locale]);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.activeComponent !== this.props.activeComponent ) {
            updateComponentsStatus(research[this.props.locale], this.props.activeComponent, 'Research');
        }
        if(prevProps.locale !== this.props.locale ) {
            T.setTexts(textContent[this.props.locale]);
        }
    }

    render() {
        const { classes, botOpen, scroller, locale } = this.props;

        return (
            <Container className={classes.root} id="research">
                <Container className={classes.container}>
                <Container className={clsx({[classes.contentShift]: botOpen}, classes.innerContainer)}>
                    <Typography variant="h3" component={T.text} tag="h2" className={classes.pageSubTitle}
                                text="main_subtitle"
                                icon={<IconSearch className={classes.mainIcon}/>}
                    />
                    <Typography variant="h1" component={T.text} tag="h1" className={classes.pageTitle}
                                text="main_title"
                    />
                    <Grid container className={classes.gridContainer}>
                            {research[locale].map((item, index) => (
                                <Grid item
                                      ref={(section) => { scroller.setComponent('Research_'+item.id, section); }}
                                      className={classes.gridItem} key={index} xs={12} sm={6}>
                                    <Card className={clsx(classes.card, item.stateClass)} elevation={2}>
                                        <CardHeader
                                            title={item.logo ? ' ' : item.title}
                                            subheader={item.subheader}
                                            avatar={ item.logo ?
                                                <img src={item.logo} aria-label={item.title} className={classes.logo} />
                                                : ''}
                                            className={classes.header}
                                        />
                                        <CardContent>
                                            <Typography variant="body2" component="p" className={classes.text}>
                                                {item.description}
                                            </Typography>
                                            <Typography variant="body2" component="p" className={classes.text} gutterBottom>
                                                {item.technology}
                                            </Typography>
                                            <Typography variant="body2" component="p" className={classes.statusText} color={item.statusBool ? "primary": "error"}>
                                                {item.status}
                                            </Typography>
                                        </CardContent>
                                    </Card>
                                </Grid>
                            ))}
                    </Grid>
                </Container>
                </Container>
            </Container>
        );
    }
}


Research.propTypes = {
    classes: PropTypes.object.isRequired,
    botOpen: PropTypes.bool.isRequired,
    scroller: PropTypes.object.isRequired,
    activeComponent: PropTypes.array,
    locale: PropTypes.string.isRequired,
};

export default withStyles(styles)(Research);
