import React from 'react';
import { Link } from "react-router-dom";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import clsx from "clsx";
import { MDText } from 'i18n-react';

import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';

import IconLocalBar from '@material-ui/icons/LocalBar'

import pattern from '../../images/paterns/flora.jpg';
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";

import { drawerWidth, drawerMargin, containerMWdrawer, cardShiftSize } from '../styleDefinitions';
import { updateComponentsStatus } from "../../js/utilityFunctions";

import { events, textContent } from "./eventsTexts";

const T = new MDText(textContent.pt, { MDFlavor: 1 });


const styles = (theme) => ({
    root: {
        flexGrow: 1,
        backgroundImage: 'url('+pattern+')',
        backgroundRepeat: 'repeat',
        backgroundAttachment: 'fixed',
        padding: 0,
        margin: 0,
        width: '100%',
        maxWidth: '100%',
        boxShadow: 'inset 0px 1px 8px 4px rgba(0,0,0)',
    },
    container: {
        flexGrow: 1,
        width: '100%',
        maxWidth: '100%',
        backgroundImage: 'radial-gradient(#090909ed, #090909b5, transparent)',
        backgroundAttachment: 'fixed',
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(8),
        margin: 0,
    },
    innerContainer: {
        padding: 0,
    },
    pageSupTitle: {
        color: 'white',
        // marginLeft: theme.spacing(2),
        marginLeft: theme.spacing(1.5),
    },
    pageTitle: {
        color: 'white',
        // marginLeft: theme.spacing(2),
        marginBottom: theme.spacing(5),
        marginLeft: theme.spacing(1),
    },
    mainIcon: {
        verticalAlign: 'top',
        position: 'absolute',
        color: '#b4acdd15',
        fontSize: '130%',
        marginTop: '-15px',
        // float: 'right',
        // display: 'inline-block',
        marginLeft: 270,
    },
    gridContainer: {
        width: 'calc(100% + '+theme.spacing(2.5)+'px)',
        // marginRight: '0px !important',
        margin: -theme.spacing(1.5).toString() +'px !important',
    },
    gridItem: {
        [theme.breakpoints.down('md')]: {
            padding: theme.spacing(2).toString() +'px !important',
        },
        [theme.breakpoints.up('md')]: {
            padding: theme.spacing(2.5).toString() +'px !important',
        },
    },
    card: {
        height: '100%',
        backgroundColor: '#ffffffd7',

        boxShadow:  '0px 4px 5px -2px rgba(0,0,0,0.38), ' +
                    '0px 7px 10px 1px rgba(0,0,0,0.47), ' +
                    '0px 2px 16px 1px rgba(0,0,0,0.45)',

        '&:hover, &.active, &:active': {
            backgroundColor: '#fffffff5',
            boxShadow:  '0px 4px 5px -2px rgba(0,0,0,0.6), ' +
                        '0px 7px 10px 1px rgba(0,0,0,0.74), ' +
                        '0px 2px 16px 1px rgba(0,0,0,0.72)',
            marginTop: -cardShiftSize,
            marginBottom: cardShiftSize,
        },

        transition: theme.transitions.create(['margin', 'background-color', 'box-shadow'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    header: {
        display: 'block',
    },
    logo: {
        height: 60,
        display: 'block',
    },
    cardImage: {
        height: 215,
    },
    contentShift: {
        transition: theme.transitions.create('padding', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        paddingRight: drawerWidth + drawerMargin,
        paddingLeft: drawerMargin,
        maxWidth: containerMWdrawer,
    },
});

class Events extends React.Component {
    state = {
    };

    componentDidMount() {
        updateComponentsStatus(events[this.props.locale], this.props.activeComponent, 'Events');
        T.setTexts(textContent[this.props.locale]);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.activeComponent !== this.props.activeComponent ){
            updateComponentsStatus(events[this.props.locale], this.props.activeComponent, 'Events');
        }
        if(prevProps.locale !== this.props.locale ) {
            T.setTexts(textContent[this.props.locale]);
        }
    }


    render() {
        const { classes, botOpen, scroller, locale } = this.props;

        return (
            <Container className={classes.root} id="events">
                <Container className={classes.container}>
                <Container className={clsx({[classes.contentShift]: botOpen}, classes.innerContainer)}>
                    <Typography variant="h3" component={T.text} tag="h2" className={classes.pageSupTitle}
                                text="main_subtitle"
                    />
                    <Typography variant="h1" component={T.text} tag="h1" className={classes.pageTitle} gutterBottom
                                icon={<IconLocalBar className={classes.mainIcon}/>}
                                text="main_title"
                    />
                    <Grid container spacing={5} className={classes.gridContainer}>
                        {events[locale].map((event, index) => (
                            <Grid item
                                  ref={(section) => { scroller.setComponent('Events_'+event.id, section); }}
                                  className={classes.gridItem} key={index} xs={12} sm={6}>
                                <Card className={clsx(classes.card, event.stateClass)} elevation={7}>
                                    <CardHeader
                                        avatar={<img src={event.logo} aria-label={event.title} className={classes.logo} />}
                                        subheader={event.subheader}
                                        className={classes.header}
                                    />
                                    {event.external ? (
                                        <CardMedia
                                            component='a'
                                            href={event.href}
                                            className={classes.cardImage}
                                            image={event.banner}
                                            title={event.banner_alt}
                                        />
                                    ) : (
                                        <CardMedia
                                            component={Link}
                                            to={event.to}
                                            className={classes.cardImage}
                                            image={event.banner}
                                            title={event.banner_alt}
                                        />
                                    )}
                                    <CardContent>
                                        <Typography component="p" className={classes.text}>
                                            {event.text}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
                </Container>
            </Container>
        );
    }
}


Events.propTypes = {
    classes: PropTypes.object.isRequired,
    botOpen: PropTypes.bool.isRequired,
    scroller: PropTypes.object.isRequired,
    activeComponent: PropTypes.array,
    locale: PropTypes.string.isRequired,
};

export default withStyles(styles)(Events);
