import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import clsx from "clsx";
import { MDText } from 'i18n-react';

import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';

import colorBlue from "@material-ui/core/colors/indigo";
import IconMusicNote from "@material-ui/icons/MusicNote";

import pattern from '../../images/paterns/ClassicGroupOf9.png';
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";

import { drawerWidth, drawerMargin, containerMWdrawer, cardShiftSize } from '../styleDefinitions';
import { updateComponentsStatus } from "../../js/utilityFunctions";

import { textContent, musics } from "./musicTexts";
import {systems} from "./systemsTexts";


const T = new MDText(textContent.pt, { MDFlavor: 1 });


const styles = (theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: colorBlue[800],
        backgroundImage: 'url('+pattern+')',
        backgroundAttachment: 'fixed',
        backgroundRepeat: 'repeat',
        margin: 0,
        width: '100%',
        maxWidth: '100%',
        boxShadow: 'inset 0px 0px 8px 5px rgba(0,0,0,0.40)',
        padding: 0,
    },
    container: {
        flexGrow: 1,
        width: '100%',
        maxWidth: '100%',
        backgroundImage: 'radial-gradient(#28348a61, #0000007a)',
        backgroundAttachment: 'fixed',
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(7),
        margin: 0,
    },
    innerContainer: {
        paddingLeft: theme.spacing(1),
        paddingRight: theme.spacing(1.5),
        width: '99.9%',
    },
    pageTitle: {
        margin: 0,
        color: 'white',
        marginTop: theme.spacing(1),
        // marginLeft: theme.spacing(2),
    },
    mainIcon: {
        verticalAlign: 'top',
        position: 'absolute',
        color: '#baacdd27',
        fontSize: '112%',
        marginTop: '-10px',
        marginLeft: -10,
    },
    subTitle: {
        color: 'white',
        marginBottom: theme.spacing(6),
        // marginLeft: theme.spacing(2),
    },
    gridItem: {
        marginBottom: theme.spacing(1),
    },
    card: {
        height: '100%',
        backgroundColor: '#ffffffd2',

        '&:hover, &.active, &:active': {
            backgroundColor: '#fffffff3',
            boxShadow:  '0px 6px 6px -3px rgba(0,0,0,0.3), ' +
                        '0px 9px 14px 1px rgba(0,0,0,0.24), ' +
                        '0px 4px 18px 3px rgba(0,0,0,0.22)',
            marginTop: -cardShiftSize,
            marginBottom: cardShiftSize,
        },

        transition: theme.transitions.create(['margin', 'background-color', 'box-shadow'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    videoWrapper : {
        position: 'relative',
        paddingBottom: '50%', /* 16:9 */
        paddingTop: 25,
        height: 0,

        '& > iframe': {
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
        },
    },
    iframe: {
        marginLeft: -theme.spacing(3),
        marginRight: -theme.spacing(3),
        width: '109%',
        marginBottom: theme.spacing(2),
    },
    contentShift: {
        transition: theme.transitions.create('padding', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        paddingRight: drawerWidth + drawerMargin,
        paddingLeft: drawerMargin,
        maxWidth: containerMWdrawer,
    },
});

class Music extends React.Component {
    state = {
    };

    componentDidMount() {
        updateComponentsStatus(musics[this.props.locale], this.props.activeComponent, 'Music');
        T.setTexts(textContent[this.props.locale]);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.activeComponent !== this.props.activeComponent ) {
            updateComponentsStatus(musics[this.props.locale], this.props.activeComponent, 'Music');
        }
        if(prevProps.locale !== this.props.locale ) {
            T.setTexts(textContent[this.props.locale]);
        }
    }

    render() {
        const { classes, botOpen, scroller, locale } = this.props;

        return (
            <Container className={classes.root} id="music">
                <Container className={classes.container}>
                <Container className={clsx({[classes.contentShift]: botOpen}, classes.innerContainer)}>
                    <Typography variant="h1" component={T.text} tag="h1" className={classes.pageTitle}
                                icon={<IconMusicNote className={classes.mainIcon}/>}
                                text="main_title"
                    />
                    <Typography variant="h3" component={T.text} tag="h2" className={classes.subTitle}
                                text="main_subtitle"
                    />
                    <Grid container spacing={4}>
                        {musics[locale].map((music, key) => (
                            <Grid item
                                  ref={(section) => { scroller.setComponent('Music_'+music.id, section); }}
                                  className={classes.gridItem} key={key} xs={12} sm={6} md={4}>
                                <Card className={clsx(classes.card, music.stateClass)} elevation={6}>
                                    <CardHeader
                                        title={music.title}
                                        subheader={music.subheader}
                                        avatar={ music.avatar ?
                                            <img src={music.avatar} aria-label={music.avatar} className={classes.avatar} />
                                            : ''}
                                        className={classes.header}
                                    />
                                    {music.media ?
                                        <div className={classes.videoWrapper}>
                                            {music.media}
                                        </div>
                                    : '' }
                                    <CardContent>
                                        <Typography variant="body2" component="p" className={classes.text}>
                                            {music.description}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
                </Container>
            </Container>
        );
    }
}


Music.propTypes = {
    classes: PropTypes.object.isRequired,
    botOpen: PropTypes.bool.isRequired,
    scroller: PropTypes.object.isRequired,
    activeComponent: PropTypes.array,
    locale: PropTypes.string.isRequired,
};

export default withStyles(styles)(Music);
