//Funcções utilitárias escitas por mim

export function updateComponentsStatus(list, activeComponent, area) {
    if (activeComponent && activeComponent[0] === area) {
        list.forEach( (entry) => {
            if( activeComponent[1] === entry.id) {
                entry.stateClass = 'active';
            } else {
                entry.stateClass = 'inactive';
            }
        });
    } else {
        list.forEach( (entry) => {entry.stateClass = 'normal'});
    }
}
