import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { MDText } from 'i18n-react';

import Container from '@material-ui/core/Container';
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import Typography from '@material-ui/core/Typography';
import Grid from "@material-ui/core/Grid";

import pattern from '../../images/paterns/circuit.png';

import IconDeveloperBoard from '@material-ui/icons/DeveloperBoard';
import clsx from "clsx";

import { drawerWidth, drawerMargin, containerMWdrawer, cardShiftSize } from '../styleDefinitions';
import { updateComponentsStatus } from "../../js/utilityFunctions";

import { textContent, projects } from "./electronicsTexts";

const T = new MDText(textContent.pt, { MDFlavor: 1 });


const styles = (theme) => ({
    root: {
        flexGrow: 1,
        backgroundImage: 'url('+pattern+')',
        backgroundRepeat: 'repeat',
        // backgroundAttachment: 'fixed',
        margin: 0,
        padding: 0,
        width: '100%',
        maxWidth: '100%',
    },
    container: {
        flexGrow: 1,
        width: '100%',
        maxWidth: '100%',
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(7),
        margin: 0,
    },
    innerContainer: {
        padding: 0,
    },
    pageSupTitle: {
        marginLeft: theme.spacing(1.5),
    },
    pageTitle: {
        marginLeft: theme.spacing(1),
    },
    gridContainer: {
        width: 'calc(100% + '+theme.spacing(2.5)+'px)',
        // marginRight: '0px !important',
        margin: -theme.spacing(1.5).toString() +'px !important',
    },
    gridItem: {
        [theme.breakpoints.down('md')]: {
            padding: theme.spacing(2).toString() +'px !important',
        },
        [theme.breakpoints.up('md')]: {
            padding: theme.spacing(2.5).toString() +'px !important',
        },
    },
    card: {
        color: 'rgba(0, 0, 0, 0.87)',
        height: '100%',
        backgroundColor: '#ffffff53',

        '&:hover, &.active, &:active': {
            backgroundColor: '#ffffffa0',
            boxShadow: '0px 2px 5px -1px rgba(0,0,0,0.2), 0px 4px 6px 0px rgba(0,0,0,0.14), 0px 1px 11px 0px rgba(0,0,0,0.12)', //el_5
            marginTop: -cardShiftSize,
            marginBottom: cardShiftSize,
        },

        transition: theme.transitions.create(['margin', 'background-color', 'box-shadow'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    mainIcon: {
        verticalAlign: 'top',
        position: 'absolute',
        color: '#76508f24',
        fontSize: '140%',
        marginTop: '-24px',
        marginLeft: '-14px',
    },
    textBold: {
        fontWeight: 500,
    },
    contentShift: {
        transition: theme.transitions.create('padding', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        paddingRight: drawerWidth + drawerMargin,
        paddingLeft: drawerMargin,
        maxWidth: containerMWdrawer,
    },
});


class Electronics extends React.Component {
    state = {
    };

    componentDidMount() {
        updateComponentsStatus(projects[this.props.locale], this.props.activeComponent, 'Electronics');
        T.setTexts(textContent[this.props.locale]);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.activeComponent !== this.props.activeComponent ) {
            updateComponentsStatus(projects[this.props.locale], this.props.activeComponent, 'Electronics');
        }
        if(prevProps.locale !== this.props.locale ) {
            T.setTexts(textContent[this.props.locale]);
        }
    }

    render() {
        const { classes, botOpen, scroller, locale } = this.props;

        return (
            <Container className={classes.root} id="electronics">
                <Container className={classes.container}>
                <Container className={clsx({[classes.contentShift]: botOpen}, classes.innerContainer)}>
                    <Typography variant="h3" component={T.text} tag="h2" className={classes.pageSupTitle}
                                text="main_subtitle"
                    />
                    <Typography variant="h1" component={T.text} tag="h1" className={classes.pageTitle} gutterBottom
                                icon={<IconDeveloperBoard className={classes.mainIcon}/>}
                                text="main_title"
                    />
                    <Grid container spacing={5} className={classes.gridContainer}>
                        {projects[locale].map( (item, index) => (
                            <Grid item
                                  ref={(section) => { scroller.setComponent('Electronics_'+item.id, section); }}
                                  className={classes.gridItem}
                                  key={index}
                                  xs={12} sm={6}>
                                <Card
                                    className={clsx(classes.card, item.stateClass)}
                                    elevation={2}>
                                    <CardHeader
                                        title={item.title}
                                        subheader={item.subtitle}
                                        className={classes.header}
                                    />
                                    <CardContent>
                                        <Typography variant="body2" component="p" className={classes.text}>
                                            {item.text}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
                </Container>
            </Container>
        );
    }
}


Electronics.propTypes = {
    classes: PropTypes.object.isRequired,
    botOpen: PropTypes.bool.isRequired,
    scroller: PropTypes.object.isRequired,
    activeComponent: PropTypes.array,
    locale: PropTypes.string.isRequired,
};

export default withStyles(styles)(Electronics);
