import React from 'react';
import { useTheme } from '@material-ui/core/styles';
import clsx from 'clsx';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import scrollToComponent from 'react-scroll-to-component';
import { MDText } from 'i18n-react';

import Services from './Index/Services';
import Systems from './Index/Systems';
import Research from './Index/Research';
import Music from './Index/Music';
import MusicProjects from './Index/MusicProjects';
import Electronics from './Index/Electronics';
import Events from "./Index/Events";

import Chatbot from "../chatBot/Chatbot";

import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import AppBar from "@material-ui/core/AppBar";
import Avatar from '@material-ui/core/Avatar';
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import IconButton from '@material-ui/core/IconButton';
import Drawer from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

import IconWork from "@material-ui/icons/Work";
import IconImportantDevices from "@material-ui/icons/ImportantDevices";
import IconSearch from '@material-ui/icons/Search'
import IconMusicNote from "@material-ui/icons/MusicNote";
import IconGroup from "@material-ui/icons/Group";
import IconLocalBar from '@material-ui/icons/LocalBar'
import IconDeveloperBoard from '@material-ui/icons/DeveloperBoard';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';

import vector_head from "../images/vector_head.svg";
import vector_head_transp from "../images/vector_head_transp.svg";
import profile_avatar from "../images/profile.png";

import br_flag from '../images/icons/brazil.png';
import us_flag from '../images/icons/united-states.png'

import robot_avatar from "../chatBot/animations/main.gif";

import useStyles from './IndexStyle';
import useLogosStyles from "./logosStyle";

// import { init, initParticleSystem } from "../libs/pointerCanvasLines";

let components = {};

const br_countries = ['BR', 'ES', 'PT', 'MZ', 'AO', 'AR', 'CL', 'CO', 'CR', 'CU', 'EC', 'GT', 'MX', 'NI', 'PE', 'UY', 'VE'];
const en_countries = ['US', 'GB', 'UK', 'AU', 'AG', 'BS', 'BB', 'BZ', 'CA', 'DM', 'GD', 'GY', 'IE', 'JM', 'NZ', 'LC', 'KN', 'VC', 'TT'];


export const textContent = {
    pt: {
        main_subtitle: "Portfólio Virtual",
        main_theme: "Desenvolvimento de Sistemas e Sites, Música e Eventos",
        bot_talk: "Converse comigo!!",
        slogan: "As melhores práticas no desenvolvimento, tanto artístico quanto tecnológico.",
        me: "Eu:",
        services: "Serviços",
        systems: "Sistemas",
        research: "Pesquisas",
        music: "Músicas",
        bands: "Bandas",
        events: "Eventos",
        electronics: "Eletrônica",
    },
    en: {
        main_subtitle: "Virtual Portfolio",
        main_theme: "Systems and Websites Development, also Music e Events",
        bot_talk: "\r Speak with me!!",
        slogan: "Best practices at the artistic and technological development.",
        me: "Me:",
        services: "Services",
        systems: "Systems",
        research: "Research",
        music: "Music",
        bands: "Bands",
        events: "Events",
        electronics: "Electronics",
    }
};

const T = new MDText(textContent.pt, { MDFlavor: 1 });


function Index(props) {
    const classes = useStyles();
    const logosClasses = useLogosStyles();

    const [ langAnchorEl, setlangAnchorEl ] = React.useState(null); //Robot talk baloon element
    const langMenuOpen = Boolean(langAnchorEl);

    const [ botAnchorEl, setBotAnchorEl ] = React.useState(null); //Robot talk baloon element
    const [ popperOpen, setPopperOpen   ] = React.useState(false); //Robot talk baloon opened bool
    const [ botOpen, setBotOpen         ] = React.useState(false);

    const [ activeComponent, setActiveComponent ] = React.useState(null); // Seta o card que estará ativo

    const [ locale, setLocale   ] = React.useState('pt'); // Idioma que será usado nas traduções
    const [ country, setCountry ] = React.useState(null); // País do usuário

    // Get the locale (based on IP and a list of countries at the top of the file)
    if (!country) {
        fetch('http://ip-api.com/json')
            .then(response => {
                return response.json();
            })
            .then(data => {
                const code = data.countryCode;
                setCountry(code);
                if (br_countries.includes(code)) {
                    setLocale('pt');
                    T.setTexts(textContent.pt);
                }
                else if(en_countries.includes(code)) {
                    setLocale('en');
                    T.setTexts(textContent.en);
                }
                console.log(code);
            })
            .catch(err => {
                console.error('Unable to consult the country of the user');
            });
    }

    function handleLangMenu(event) {
        setlangAnchorEl(event.currentTarget);
    }
    function handleLangClose() {
        setlangAnchorEl(null);
    }

    const changeLocale = (newLocale) => {
        setLocale(newLocale);
        T.setTexts(textContent[newLocale]);
        setlangAnchorEl(null);
    };

    const scrollTrigger = useScrollTrigger({ // Scroll trigger que controla quando a barra devera se fixar no topo
        threshold: 519,
        disableHysteresis: true,
    });

    const theme = useTheme();
    const scrollOptions = {
        offset: - (theme.spacing(8) - 1), // tamanho da app bar
        align: 'top',
        duration: 1500
    };
    const scrollOptions2 = {
        offset: - (theme.spacing(8) - 1) - 18, // + espaço entre card e barra
        align: 'top',
        duration: 1500
    };

    const scroller = {
        setComponent: (name, section) => {
            components[name] = section;
        },
        scrollTo: (name, options = scrollOptions) => {
            scrollToComponent(components[name], options)
        },
    };

    const goToComponent = (area, subpart = null ) => {
        if(!area) {
            setActiveComponent(null); // opção de sair do componente atual (desativar todos)
        }
        else if(!subpart) {
            setActiveComponent(null);
            scroller.scrollTo(area)
        }
        else {
            scroller.scrollTo(area+'_'+subpart, scrollOptions2);
            setActiveComponent([area, subpart]);
        }
    };

    let robotLoadHandler = (event) => {
        setBotAnchorEl(event.currentTarget);
        setPopperOpen(true);
    };

    function handleBotDrawerOpen() {
        setBotOpen(true);
    }

    function handleBotDrawerClose() {
        setBotOpen(false);
    }

    // init();
    // initParticleSystem();

    return (
        <React.Fragment>
            <Paper elevation={5} className={classes.homeContainer}
                   ref={(section) => { scroller.setComponent('Index', section); }}>
                <div className={classes.bigHeadDiv}>
                    <img className={classes.bigHead} src={vector_head} alt=""/>
                </div>
                <div className={classes.homeContainerContent}>
                    <Typography variant="h1" component="h1" className={classes.mainTitleName}>
                        Rodrigo Werneck Franco
                    </Typography>
                    <Typography variant="h3" component={T.text} tag='h3' className={classes.textWhite}
                        text="main_subtitle" />
                    <Typography variant="h5" component={T.text} tag='h4' className={classes.textWhite}
                        text="main_theme" />
                    <div className={classes.avatarContiner}>
                        <Avatar alt="Foto do rosto de Rodrigo Werneck"
                                src={profile_avatar}
                                className={classes.avatar} />
                    </div>
                    {!botOpen &&
                        <div className={classes.robotAvatarContainer}>
                            <Avatar
                                onClick={handleBotDrawerOpen}
                                onLoad={robotLoadHandler}
                                alt="Foto do robô Rodrigo Werneck"
                                src={robot_avatar}
                                className={classes.robotAvatar}/>
                            <Popper open={popperOpen}
                                    anchorEl={botAnchorEl}
                                    placement={'right-start'}
                                    transition>
                                {({TransitionProps}) => (
                                    <Fade {...TransitionProps} timeout={350}>
                                        <div className={classes.robotPopperDiv}>
                                            <Paper className={classes.robotPopperPaper} elevation={0}>
                                                <Typography color='textPrimary' component={T.p} className={classes.robotTalk}
                                                text="bot_talk" />
                                            </Paper>
                                            <Typography variant="subtitle1"
                                                        className={clsx(classes.robotAvatarText, classes.textWhite)}>
                                                - WerneckBot
                                            </Typography>
                                        </div>
                                    </Fade>
                                )}
                            </Popper>
                        </div>
                    }
                    <Typography variant="h5" component={T.p} className={clsx(classes.textWhite, classes.headingNormalText)}
                        text="slogan" />
                    <Typography variant="h5" component="p" className={clsx(classes.textWhite, classes.headingNormalText)}>
                        rodrigowfranco@gmail.com | @rodrigowfranco | {country !== 'BR' ? '+55' : ''} (21) 992242233
                    </Typography>
                </div>
                <div className={classes.appBarContainer}>
                    <AppBar
                        position={scrollTrigger ? "fixed" : "static"}
                        elevation={scrollTrigger ? 6 : 0}
                        className={classes.appBar}
                    >
                        <Toolbar>
                            <Button onClick={() => scroller.scrollTo('Index')}
                                    color="inherit" className={classes.menuItem}>
                                <img src={vector_head_transp} width={24} className={classes.menuIcon} alt="ícone rosto" />
                                <T.span text="me" />
                            </Button>
                            <Button onClick={() => scroller.scrollTo('Services')}
                                    color="inherit" className={classes.menuItem}>
                                <IconWork className={classes.menuIcon}/>
                                <T.span text="services" />
                            </Button>
                            <Button onClick={() => scroller.scrollTo('Systems')}
                                    color="inherit" className={classes.menuItem}>
                                <IconImportantDevices className={classes.menuIcon}/>
                                <T.span text="systems" />
                            </Button>
                            <Button onClick={() => scroller.scrollTo('Research')}
                                    color="inherit" className={classes.menuItem}>
                                <IconSearch className={classes.menuIcon}/>
                                <T.span text="research" />
                            </Button>
                            <Button onClick={() => scroller.scrollTo('Music')}
                                    color="inherit" className={classes.menuItem}>
                                <IconMusicNote className={classes.menuIcon}/>
                                <T.span text="music" />
                            </Button>
                            <Button onClick={() => scroller.scrollTo('MusicProjects')}
                                    color="inherit" className={classes.menuItem}>
                                <IconGroup className={classes.menuIcon}/>
                                <T.span text="bands" />
                            </Button>
                            <Button onClick={() => scroller.scrollTo('Events')}
                                    color="inherit" className={classes.menuItem}>
                                <IconLocalBar className={classes.menuIcon}/>
                                <T.span text="events" />
                            </Button>
                            <Button onClick={() => scroller.scrollTo('Electronics')}
                                    color="inherit" className={classes.menuItem}>
                                <IconDeveloperBoard className={classes.menuIcon}/>
                                <T.span text="electronics" />
                            </Button>
                            <div className={classes.grow} />
                            <IconButton
                                aria-label="account of current user"
                                aria-controls="menu-appbar"
                                aria-haspopup="true"
                                onClick={handleLangMenu}
                                color="inherit"
                            >
                                {locale === 'en'
                                    ? <img src={us_flag} alt='US Flag'/>
                                    : <img src={br_flag} alt='BR Flag'/>
                                }
                            </IconButton>
                            <Menu
                                id="menu-appbar"
                                anchorEl={langAnchorEl}
                                anchorOrigin={{
                                    vertical: 'top',
                                    horizontal: 'right',
                                }}
                                keepMounted
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'right',
                                }}
                                open={langMenuOpen}
                                onClose={handleLangClose}
                            >
                                <MenuItem onClick={() => changeLocale('pt')} className={classes.languageItem}>
                                    <img src={br_flag} alt='BR Flag'/>&nbsp; pt-BR
                                </MenuItem>
                                <MenuItem onClick={() => changeLocale('en')} className={classes.languageItem}>
                                    <img src={us_flag} alt='US Flag'/>&nbsp; en-US
                                </MenuItem>
                            </Menu>
                        </Toolbar>
                    </AppBar>
                </div>
            </Paper>
            <main
                className={classes.content}
            >
                <Services       ref={(section) => { scroller.setComponent('Services', section); }}
                                botOpen={botOpen}
                                scroller={scroller}
                                activeComponent={activeComponent}
                                lgClss={logosClasses}
                                locale={locale}
                />
                <Systems        ref={(section) => { scroller.setComponent('Systems', section); }}
                                botOpen={botOpen}
                                scroller={scroller}
                                activeComponent={activeComponent}
                                locale={locale}
                />
                <Research       ref={(section) => { scroller.setComponent('Research', section); }}
                                botOpen={botOpen}
                                scroller={scroller}
                                activeComponent={activeComponent}
                                locale={locale}
                />
                <Music          ref={(section) => { scroller.setComponent('Music', section); }}
                                botOpen={botOpen}
                                scroller={scroller}
                                activeComponent={activeComponent}
                                locale={locale}
                />
                <MusicProjects  ref={(section) => { scroller.setComponent('MusicProjects', section); }}
                                botOpen={botOpen}
                                scroller={scroller}
                                activeComponent={activeComponent}
                                locale={locale}
                />
                <Events         ref={(section) => { scroller.setComponent('Events', section); }}
                                botOpen={botOpen}
                                scroller={scroller}
                                activeComponent={activeComponent}
                                locale={locale}
                />
                <Electronics    ref={(section) => { scroller.setComponent('Electronics', section); }}
                                botOpen={botOpen}
                                scroller={scroller}
                                activeComponent={activeComponent}
                                locale={locale}
                />
            </main>
            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="right"
                open={botOpen}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>
                    <IconButton onClick={handleBotDrawerClose}>
                        <ChevronRightIcon />
                    </IconButton>
                </div>
                <Divider />

                {botOpen &&
                    <Chatbot goToComponent={goToComponent} locale={locale}/>
                }
            </Drawer>
        </React.Fragment>
    );
}

export default Index;