import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { MDText } from 'i18n-react';

import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import clsx from "clsx";

import colorGreen from "@material-ui/core/colors/lightGreen";

import pattern from '../../images/paterns/flowerSeamless.png';

import IconGroup from "@material-ui/icons/Group";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";

import { drawerWidth, drawerMargin, containerMWdrawer, cardShiftSize } from '../styleDefinitions';
import { updateComponentsStatus } from "../../js/utilityFunctions";

import { textContent, projects } from "./musicProjectsTexts";
import {systems} from "./systemsTexts";

const T = new MDText(textContent.pt, { MDFlavor: 1 });


const styles = (theme) => ({
    root: {
        flexGrow: 1,
        backgroundImage: 'url('+pattern+')',
        backgroundRepeat: 'repeat',
        backgroundColor: colorGreen[200],
        // backgroundAttachment: 'fixed',
        padding: 0,
        margin: 0,
        width: '100%',
        maxWidth: '100%',
        // boxShadow: 'inset 1px 4px 7px 0px rgba(0,0,0,0.4)',
    },
    container: {
        flexGrow: 1,
        width: '100%',
        maxWidth: '100%',
        backgroundImage: 'radial-gradient('+colorGreen[200]+' 15%, transparent 99%)',
        // backgroundAttachment: 'fixed',
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(7),
        margin: 0,
    },
    innerContainer: {
        padding: 0,
    },
    pageTitle: {
        margin: 0,
        // color: 'white',
        marginTop: theme.spacing(1),
        marginLeft: theme.spacing(1),
    },
    mainIcon: {
        verticalAlign: 'top',
        position: 'absolute',
        color: '#63774336',
        fontSize: '160%',
        marginTop: -25,
        marginLeft: -5,
    },
    subTitle: {
        // color: 'white',
        marginBottom: theme.spacing(2),
        marginLeft: theme.spacing(1.5),
    },
    gridContainer: {
        width: 'calc(100% + '+theme.spacing(2.5)+'px)',
        // marginRight: '0px !important',
        margin: -theme.spacing(1.5).toString() +'px !important',
    },
    gridItem: {
        [theme.breakpoints.down('md')]: {
            padding: theme.spacing(2).toString() +'px !important',
        },
        [theme.breakpoints.up('md')]: {
            padding: theme.spacing(2.5).toString() +'px !important',
        },
    },
    card: {
        height: '100%',
        backgroundColor: '#ffffff9f',

        '&:hover, &.active, &:active': {
            backgroundColor: '#ffffffd4',
            boxShadow: '0px 4px 5px -2px rgba(0,0,0,0.2), 0px 7px 10px 1px rgba(0,0,0,0.14), 0px 2px 16px 1px rgba(0,0,0,0.12)', //el_5
            marginTop: -cardShiftSize,
            marginBottom: cardShiftSize,
        },

        transition: theme.transitions.create(['margin', 'background-color', 'box-shadow'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    statusText: {
        marginTop: theme.spacing(1),
    },
    projectImage: {
        height: 230,
    },
    contentShift: {
        transition: theme.transitions.create('padding', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        paddingRight: drawerWidth + drawerMargin,
        paddingLeft: drawerMargin,
        maxWidth: containerMWdrawer,
    },
});

class MusicProjects extends React.Component {
    state = {
    };

    componentDidMount() {
        updateComponentsStatus(projects[this.props.locale], this.props.activeComponent, 'MusicProjects');
        T.setTexts(textContent[this.props.locale]);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.activeComponent !== this.props.activeComponent ){
            updateComponentsStatus(projects[this.props.locale], this.props.activeComponent, 'MusicProjects');
        }
        if(prevProps.locale !== this.props.locale ) {
            T.setTexts(textContent[this.props.locale]);
        }
    }

    render() {
        const { classes, botOpen, scroller, locale } = this.props;

        return (
            <Container className={classes.root} id="bands">
                <Container className={classes.container}>
                <Container className={clsx({[classes.contentShift]: botOpen}, classes.innerContainer)}>
                    <Typography variant="h1" component={T.text} tag="h1" className={classes.pageTitle}
                                icon={<IconGroup className={classes.mainIcon}/>}
                                text="main_title"
                    />
                    <Typography variant="h3" component={T.text} tag="h2" className={classes.subTitle}
                                text="main_subtitle"
                    />
                    <br/>
                    <Grid container spacing={5} className={classes.gridContainer}>
                        {projects[locale].map((project, index) => (
                            <Grid item
                                  ref={(section) => { scroller.setComponent('MusicProjects_'+project.id, section); }}
                                  className={classes.gridItem} key={index} xs={12} sm={6}>
                                <Card className={clsx(classes.card, project.stateClass)} elevation={4}>
                                    <CardHeader
                                        title={project.logo ? ' ' : project.title}
                                        subheader={project.subheader}
                                        avatar={ project.logo ?
                                            <img src={project.logo} aria-label={project.title} className={classes.logo} />
                                            : ''}
                                        className={classes.header}
                                    />
                                    {project.image ?
                                        <CardMedia
                                            className={classes.projectImage}
                                            image={project.image}
                                            title={"Imagem da tela do site: "+project.title}
                                            component='a'
                                            href={project.href}
                                        />
                                        : '' }

                                    <CardContent>
                                        <Typography variant="body2" component="p" className={classes.text}>
                                            {project.description}
                                        </Typography>
                                        {project.status ?
                                            <Typography variant="body2" component="p" className={classes.statusText}
                                                        color={project.statusBool ? "primary": "error"}>
                                                {project.status}
                                            </Typography>
                                        : '' }
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
                </Container>
            </Container>
        );
    }
}


MusicProjects.propTypes = {
    classes: PropTypes.object.isRequired,
    botOpen: PropTypes.bool.isRequired,
    scroller: PropTypes.object.isRequired,
    activeComponent: PropTypes.array,
    locale: PropTypes.string.isRequired,
};

export default withStyles(styles)(MusicProjects);