import React from 'react';
import { Link } from "react-router-dom";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import clsx from "clsx";
import { MDText } from 'i18n-react';

import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import Box from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';

import IconImportantDevices from "@material-ui/icons/ImportantDevices";

import colorPurple from "@material-ui/core/colors/deepPurple";

import pattern from '../../images/paterns/unicode.png';

import { drawerWidth, drawerMargin, containerMWdrawer, cardShiftSize } from '../styleDefinitions';
import { updateComponentsStatus } from "../../js/utilityFunctions";

import { textContent, systems} from "./systemsTexts";

const T = new MDText(textContent.pt, { MDFlavor: 1 });

const styles = (theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: colorPurple[700],
        backgroundImage: 'url('+pattern+')',
        // backgroundAttachment: 'fixed',
        padding: 0,
        margin: 0,
        width: '100%',
        maxWidth: '100%',
        boxShadow: 'inset 1px 1px 4px 2px rgba(200,200,200,0.2), inset  0px -4px 8px 2px rgba(0,0,0,0.24)',
        // boxShadow: 'inset 1px 1px 8px 4px rgba(0,0,0,0.3)',
    },
    container: {
        flexGrow: 1,
        width: '100%',
        maxWidth: '100%',
        backgroundImage: 'radial-gradient(transparent, #0c041f57)',
        backgroundAttachment: 'fixed',
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(7),
        margin: 0,
    },
    innerContainer: {
        paddingLeft: theme.spacing(1),
        paddingRight: theme.spacing(1.5),
        width: '99.9%',
    },
    pageTitle: {
        color: 'white',
        marginTop: theme.spacing(1),
        // marginLeft: theme.spacing(2),
    },
    subTitle: {
        marginLeft: theme.spacing(0.5),
        marginBottom: theme.spacing(6),
        color: 'white',
    },
    gridItem: {
        marginBottom: theme.spacing(1),
    },
    card: {
        padding: 0,
        height: '100%',
        backgroundColor: '#ffffffd7',


        '&:hover:not(.inactive), &.active, &:active': {
            backgroundColor: '#fffffff3',
            boxShadow:  '0px 3px 6px -3px rgba(0,0,0,0.32), ' +
                        '0px 4px 11px 1px rgba(0,0,0,0.27), ' +
                        '0px 2px 15px 3px rgba(0,0,0,0.25)',
            marginTop: -cardShiftSize,
            marginBottom: cardShiftSize,
        },

        transition: theme.transitions.create(['margin', 'background-color', 'box-shadow'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    header: {
        display: 'block',
    },
    logo: {
        height: 50,
        display: 'block',
    },
    systemImage: {
        // height: 335,
        width: '100%',
        paddingTop: 0, // 16:9
        backgroundSize: 'contain',
    },
    mainIcon: {
        verticalAlign: 'top',
        position: 'absolute',
        color: '#baacdd23',
        fontSize: '130%',
        marginTop: '-15px',
        marginLeft: '-5px',
    },
    textBold: {
        fontWeight: 500,
    },
    statusText: {
        marginTop: theme.spacing(2),
        // margin: theme.spacing(2),
        // position: 'relative',
        // bottom: theme.spacing(2),
    },
    contentShift: {
        transition: theme.transitions.create('padding', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        paddingRight: drawerWidth + drawerMargin,
        paddingLeft: drawerMargin,
        maxWidth: containerMWdrawer,
    },
});

class Systems extends React.Component {
    state = {
    };

    componentDidMount() {
        updateComponentsStatus(systems[this.props.locale], this.props.activeComponent, 'Systems');
        T.setTexts(textContent[this.props.locale]);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.activeComponent !== this.props.activeComponent ) {
            updateComponentsStatus(systems[this.props.locale], this.props.activeComponent, 'Systems');
        }
        if(prevProps.locale !== this.props.locale ) {
            T.setTexts(textContent[this.props.locale]);
        }
    }

    render() {
        const { classes, botOpen, scroller, locale } = this.props;

        return (
            <Container className={classes.root} id="systems">
                <Container className={classes.container}>
                <Container className={clsx({[classes.contentShift]: botOpen}, classes.innerContainer)}>
                    <Typography variant="h1" component={T.text} tag="h1" className={classes.pageTitle}
                        icon={<IconImportantDevices className={classes.mainIcon}/>}
                        text="main_title"
                    />
                    <Typography variant="h3" component={T.text} tag="h2" className={classes.subTitle} gutterBottom
                        text="main_subtitle"
                    />
                    <Grid container spacing={4}>
                        {systems[locale].map((item, index) => (
                            <Grid item
                                  ref={(section) => { scroller.setComponent('Systems_'+item.id, section); }}
                                  className={classes.gridItem}
                                  key={index}
                                  xs={12} sm={6} md={4}>
                                <Card
                                    className={clsx(classes.card, item.stateClass)}
                                    elevation={6}>
                                    <CardHeader
                                        title={item.logo ? ' ' : item.title}
                                        subheader={item.subheader}
                                        avatar={ item.logo ?
                                                <img src={item.logo} aria-label={item.title} className={classes.logo} />
                                        : ''}
                                        className={classes.header}
                                    />

                                    {item.image ?
                                        <Box
                                            {...((item.link) ?
                                                ( item.external ?
                                                    {
                                                        component: 'a',
                                                        href: item.link
                                                    }
                                                    :
                                                    {
                                                        component: Link,
                                                        to: item.link
                                                    }
                                                ) : {}) }
                                        >
                                            <img
                                                src={item.image}
                                                className={classes.systemImage}
                                                alt={"Imagem da tela do site: "+item.title}
                                            />
                                        </Box>
                                    : ' ' }

                                    <CardContent>
                                        <Typography variant="body2" component="p" className={classes.text}>
                                            {item.description}
                                        </Typography>
                                        <Typography variant="body2" component="p" className={classes.text}>
                                            {item.technology}
                                        </Typography>
                                        <Typography variant="body2" component="p" className={classes.statusText} color={item.statusBool ? "primary": "error"}>
                                            {item.status}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
                </Container>
            </Container>
        );
    }
}

Systems.propTypes = {
    classes: PropTypes.object.isRequired,
    botOpen: PropTypes.bool.isRequired,
    scroller: PropTypes.object.isRequired,
    activeComponent: PropTypes.array,
    locale: PropTypes.string.isRequired,
};

export default withStyles(styles)(Systems);
