import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import {makeStyles, withStyles} from "@material-ui/core";
import { ChatFeed, Message } from 'react-chat-ui';
import axios from 'axios';
import scrollToComponent from 'react-scroll-to-component';
import Avatar from "@material-ui/core/Avatar";

import responses from './responses';

import attention_gif from "../chatBot/animations/attention.gif";
import hey_gif from "../chatBot/animations/jump_hello.gif";
import sleep_gif from "../chatBot/animations/sleep.gif";
import distracted_gif from "../chatBot/animations/strange.gif";
import talk_gif from "../chatBot/animations/talk.gif";
import run_gif from "../chatBot/animations/talk.gif";
import robot_avatar from "./animations/hello.gif";
import {textContent} from "../pages/Index/servicesTexts";

const dict = {
    'Dilce'     : 'JEWC',
    'Edilson'   : 'JEWC',
    'Dilson'    : 'JEWC',
    'Gilson'    : 'JEWC',
    'intelege'      : 'Intellegi',
    'interlegis'    : 'Intellegi',
    'Inter hoje'    : 'Intellegi',
    'interpole' : 'Inter Poli',
    'Deville'   : 'Dog Ville',
    'pulsar parte': 'Pulsar Party',
    'frango'        : 'framework',
    'frame work'    : 'framework',
    'fêmea'         : 'framework',
    'frenorte'      : 'framework',
    'fêmur'         : 'framework',
    'JCM'   : 'Ejcm',
    'nudes' : 'Nutes',
    'time-lapse': 'Timelapse',
    'sete': 'Seti',
    'Banda B'       : 'Banda Bettersweet',
    'BR Cheats'     : 'Bettersweet',
    'Backstreet'    : 'Bettersweet',
    'Bittersweet'   : 'Bettersweet',
    'bitter sweet'  : 'Bettersweet',
    'Smart City': 'Smart Sitio',
    'no Dirceu' : 'Node MCU',
    'nude Mc'   : 'Node MCU',
    'nude MCU'  : 'Node MCU',
};

const styles = {
    root: {
        marginLeft: 10,
    },
    robotAvatar: {
        display: 'inline-block',
        width: 80,
        height: 80,
        position: 'absolute',
        bottom: 0,
    },
    chatLimiter: {
        width: '93%',
        maxWidth: '93%',
        height: '87%',
        maxHeight: '87%',
        position: 'absolute',
        display: 'inline-block',
        overflow: 'hidden',
    },
    chatWrapper: {
        position: 'absolute',
        bottom: 80,
    }
};

const langs = { en: 'en-US', pt: 'pt-BR' };

//Todo fallback error message quando o meu backend estiver fora do ar

class Chatbot extends React.Component {
    state = {
        is_typing: false,
        is_playing: true,
        current_animation: talk_gif,
        messages: [
            new Message({
                id: 1,
                message: responses['bemvindo'].text,
            }) // Gray bubble
        ],
    };

    componentDidMount() {
        this.rootDiv = document.getElementById("chatRoot");
        this.annyang = window.annyang;
        if (!this.annyang) {
            console.error('Erro! lib de reconhecimento de audio ausente.');
            return;
        }
        this.annyang.setLanguage(langs[this.props.locale]);
        this.annyang.addCallback('soundstart', this.voiceStartCallback);
        this.annyang.addCallback('result', this.voiceRecogCallback);
        this.audioPlayer = document.getElementById("robotVoicePlayer");
        this.audioPlayer.onended = this.playEndCallback;
        this.playVoice('bemvindo');
        axios.get('http://localhost:5000/connect/'+this.props.locale)
            .then( (res) => {
                console.log(res.data)
            })
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.locale !== this.props.locale ){
            this.annyang.setLanguage(langs[this.props.locale]);
        }
    }

    voiceStartCallback = () => {
        if ( !this.state.is_playing ) {
            this.setState({ current_animation: attention_gif });
        }
    };

    voiceRecogCallback = (phrases) => {
        // console.log("I think the user said: ", phrases[0]);
        // console.log("But then again, it could be any of the following: ", phrases);
        let result = phrases[0];
        Object.keys(dict).forEach((key) => {
            result = result.replace(key, dict[key]);
        });
        this.add_message(0, result);
        this.sendToAssistant(result);
    };

    sendToAssistant(message) {
        axios.post('http://localhost:5000/hear/', {message: message})
            .then( (res) => {
                if(res.data) {
                    this.responseCallback(res.data);
                }
            })
    }

    responseCallback(inData) {
        if(inData.output.generic[0].text) {
            let resName = inData.output.generic[0].text;
            let response = responses[this.props.locale][resName];
            if (response.text) {
                this.add_message(
                    1,
                    response.text
                );
            }
            const animation = response.anim ? response.anim : talk_gif;
            this.setState({ current_animation: animation }); // Animação da fala
            this.playVoice(resName);
            if(response.area) { // Rolar para secao do site
                if(response.has_entity && inData.output.entities[0].value) {
                    const area = response.area;
                    const subpart = inData.output.entities[0].value;
                    this.props.goToComponent(area, subpart);
                }
                else {
                    this.props.goToComponent(response.area);
                }
            }
            this.rootDiv.scrollTop = this.rootDiv.scrollHeight; // Rolar para baixo no chat
        }
    }

    add_message = (id, message) => {
        this.setState({
            messages:
                this.state.messages.concat([
                    new Message({
                        id: id, message: message
                    })
                ])
        });
    };

    playVoice = (name) => {
        this.setState({ is_playing: true });
        this.annyang.pause();
        this.audioPlayer.src = '/voices/'+name+'.mp3';
        this.audioPlayer.play();
    };

    playEndCallback = () => { // Quando o audio (voz do robô) TERMINA de tocar
        this.setState({ is_playing: false });
        this.annyang.start();
        this.setState({ current_animation: distracted_gif });
        this.props.goToComponent(null); // Desativa card que estava sendo descrito (caso tivesse algum)
    };


    render() {
        const { classes } = this.props;

        return (
            <div id="chatRoot" className={classes.root}>
                <div className={classes.chatLimiter}>
                <div className={classes.chatWrapper}>
                    <ChatFeed
                        messages={this.state.messages} // Boolean: list of message objects
                        isTyping={this.state.is_typing} // Boolean: is the recipient typing
                        hasInputField={false} // Boolean: use our input, or use your own
                        showSenderName // show the name of the user who sent the message
                        bubblesCentered={false} //Boolean should the bubbles be centered in the feed?
                        // JSON: Custom bubble styles
                        bubbleStyles={
                            {
                                text: {
                                    fontSize: 16,
                                    fontWeight: 500
                                },
                                chatbubble: {
                                    borderRadius: 8,
                                    padding: 8
                                }
                            }
                        }
                    />
                </div>
                </div>
                <Avatar
                    alt="Avatar animado do robô Rodrigo Werneck"
                    src={this.state.current_animation}
                    className={classes.robotAvatar} />
                <audio id='robotVoicePlayer'> </audio>
            </div>
        );
    }
}

Chatbot.propTypes = {
    classes: PropTypes.object.isRequired,
    goToComponent: PropTypes.func.isRequired,
    locale: PropTypes.string.isRequired,
};

export default withStyles(styles)(Chatbot);

